.whitecard-outline {
background-color: var(--sec-background-color);
outline: 1px solid var(--card-outline);
padding: 1rem;
position: relative;
height: calc(100% - 2rem);
grid-column: span 3;

  @media screen and (max-width: 1124px) {
  grid-column: span 4;
  }

  @media screen and (max-width: 768px) {
  grid-column: span 12;
  }

  @media screen and (max-width: 480px) {
  grid-column: span 12;
  }

	small {
	color: $dark-blue;
	}
}

.whitecard {
background-color: var(--sec-background-color);
padding: 1rem 1rem 1rem 0rem;
position: relative;
height: calc(100% - 2rem);
grid-column: span 3;

  @media screen and (max-width: 1124px) {
  grid-column: span 4;
  }

  @media screen and (max-width: 768px) {
  grid-column: span 12;
  }

  @media screen and (max-width: 480px) {
  grid-column: span 12;
  }

	small {
	color: $dark-blue;
	}
}


/*=============== Full Clickable Cards =================== */

.click-cards {

    h3 {
      margin: 3rem 0rem 1rem;
      padding-bottom: .5rem;
      &:not(:first-child) {
        margin-top: 3rem;
      }
    }

    a, a:hover, a:focus {
      text-decoration: none;
    }

    ul.list-item {
    grid-gap: 1.5em;
    list-style: none;
    padding: 0;
    margin-top: 0;
    }

    ul.list, ul.list-item {
      list-style: none;
      padding: 0;
    }

    .card {
      border: 1px solid var(--card-outline);
      border-radius: 10px;
      padding: 1rem;
      position: relative;
      height: calc(100% - 2rem);
      grid-column: span 3;

        @media screen and (max-width: 1300px) {
         grid-column: span 6;
        }

        @media screen and (max-width: 768px) {
          grid-column: span 12;
        }

        @media screen and (max-width: 480px) {
          grid-column: span 12;
        }

      > a {
        position: absolute;
        top: 0; left: 0;
        height: 100%; width: 100%;
        }

      .title {
        font-size: 1.25rem;
        font-weight: 600;
        }

      .description {
        margin: 1rem 0 1.5rem 0;
        font-size: 1rem;
        line-height: 1.3rem;
        }
        
      .metatext {
        margin: 0;
        font-size: .7rem;
        line-height: 1.1rem;
        }

      img {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 90%;
        padding: 1rem 0 2rem 0;
        }

      span.highlighted {
        font-weight: bold;
        font-style: italic;
        line-height: inherit;
      }

      .content-highlights p {
        font-size: .7rem;
        line-height: .8rem;
        opacity: 0.8;
      }

      &:hover, &:focus {
        background-color: var(--card-background-color-hover);
        border:1px solid var(--card-background-color-hover);
      }
    }

    .card-static {
      border: 1px solid var(--card-outline);
      border-radius: 10px;
      padding: 1rem;
      position: relative;
      height: calc(100% - 2rem);
      grid-column: span 3;

        @media screen and (max-width: 1300px) {
         grid-column: span 6;
        }

        @media screen and (max-width: 768px) {
          grid-column: span 12;
        }

        @media screen and (max-width: 480px) {
          grid-column: span 12;
        }

      > a {
        position: absolute;
        top: 0; left: 0;
        height: 100%; width: 100%;
        }

      .title {
        font-size: 1.25rem;
        font-weight: 600;
        }

      .description {
        margin: 1rem 0 1.5rem 0;
        font-size: 1rem;
        line-height: 1.3rem;
        }
        
      .metatext {
        margin: 0;
        font-size: .7rem;
        line-height: 1.1rem;
        }
    }

}
